//API
//
//
GM.Form = {};

/**
 * @description 
 * @class GM.Form.CommonOper
 */
GM.Form.CommonOper = {
    /**
     * 
     * @param {String} msg 
     */
    showSuccessMsg: function (msg) {
        GM.Infomation.show(msg, {
            type: 'success',
            placement: 'center'
        });
    },
    /**
     * 
     * @param {String} msg 
     */
    showFailureMsg: function (msg) {
        GM.Infomation.show(msg, {
            type: 'danger',
            placement: 'center'
        });
    },
    getUUID: function () {
        var chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
        var uuid = [],
            i;
        var radix = 16 || chars.length;

        if (32) {
            // Compact form
            for (i = 0; i < 32; i++) {
                uuid[i] = chars[0 | Math.random() * radix];
            }
        } else {
            // rfc4122, version 4 form
            var r;

            // rfc4122 requires these characters
            uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
            uuid[14] = '4';

            // Fill in random data. At i==19 set the high bits of clock sequence
            // as
            // per rfc4122, sec. 4.1.5
            for (i = 0; i < 36; i++) {
                if (!uuid[i]) {
                    r = 0 | Math.random() * radix;
                    uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
                }
            }
        }

        return uuid.join('');
    },
    /**
     * 
     * @param url URL
     * @param size lgsmfullscreen''
     * @param reload 
     */
    showDialog: function (url, title, size, callback) {
        var w, h;

        var option = {
            iframe: url + ((url.indexOf('?') > -1 ? '&' : '?') + 'version=' + new Date()),
            title: title,
            backdrop: 'static'
        };

        if (size && size.indexOf(',') > -1) {
            var arrSize = size.split(',');

            try {
                w = parseInt(arrSize[0]) + 'px';
            } catch (e) {
                w = '1000px';
            }

            try {
                h = parseInt(arrSize[1]) + 'px';
            } catch (e) {
                h = '800px';
            }

            option['width'] = w;
            option['height'] = h;
        } else {
            option['size'] = size;
        }

        var myModalTrigger = new $.zui.ModalTrigger(option);

        myModalTrigger.show({
            hidden: function () {
                if (callback) {
                    callback();
                }
            }
        });
    },
    /**
     * 
     * @param array
     * @param field
     * @returns {Array}
     */
    mapFieldArray: function (array, field) {
        var result = [];

        for (var i = 0; i < array.length; i++) {
            if (typeof (array[i]) === 'object' && array[i][field]) {
                result.push(array[i][field]);
            }
        }

        return result;
    },
    /**
     * GET
     *
     * @param {String} url                  url
     * @param {Function} successCallback    
     * @param {Function} errorCallback      
     * @param {Object} param                
     */
    syncAjaxGet: function (url, successCallback, errorCallback, param) {
        this._syncAjax('GET', url, successCallback, errorCallback, param);
    },
    /**
     * POST
     *
     * @param {String} url                  url
     * @param {Function} successCallback    
     * @param {Function} errorCallback      
     * @param {Object} param                
     */
    syncAjaxPost: function (url, successCallback, errorCallback, param) {
        this._syncAjax('POST', url, successCallback, errorCallback, param);
    },
    _syncAjax: function (type, url, successCallback, errorCallback, param) {
        var opt = {
            url: url,
            async: false,
            type: type,
            success: function (res) {
                successCallback(res);
            },
            error: function (res) {
                errorCallback(res);
            }
        };

        if (param) {
            opt.data = param;
        }

        $.ajax(opt);
    },
    /**
     * 
     * @param {Number} n
     */
    getCnFormatMoney: function (n) {
        var fraction = ['', ''];
        var digit = [
            '', '', '', '', '',
            '', '', '', '', ''
        ];
        var unit = [
            ['', '', ''],
            ['', '', '', '']
        ];
        var head = n < 0 ? '' : '';
        n = Math.abs(n);
        var s = '';
        for (var i = 0; i < fraction.length; i++) {
            s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/./, '');
        }
        s = s || '';
        n = Math.floor(n);
        for (var i = 0; i < unit[0].length && n > 0; i++) {
            var p = '';
            for (var j = 0; j < unit[1].length && n > 0; j++) {
                p = digit[n % 10] + unit[1][j] + p;
                n = Math.floor(n / 10);
            }
            s = p.replace(/(.)*$/, '').replace(/^$/, '') + unit[0][i] + s;
        }
        return head + s.replace(/(.)*/, '')
            .replace(/(.)+/g, '')
            .replace(/^$/, '');
    },
    setObjectAttrVal: function (obj, exp, index, val) {
        if (!obj) {
            return;
        }

        var arr = exp.split('.');

        if (index == arr.length - 1) {
            obj[arr[index]] = val;
            return;
        }

        obj = obj[arr[index]];
        index++;

        this.setObjectAttrVal(obj, exp, index, val);
    },
    cloneObject: function (obj) {
        // Handle the 3 simple types, and null or undefined
        if (null == obj || 'object' !== typeof obj) return obj;

        // Handle Date
        if (obj instanceof Date) {
            var copy = new Date();
            copy.setTime(obj.getTime());
            return copy;
        }

        // Handle Array
        if (obj instanceof Array) {
            var copy = [];
            for (var i = 0, len = obj.length; i < len; ++i) {
                copy[i] = GM.Form.CommonOper.cloneObject(obj[i]);
            }
            return copy;
        }

        // Handle Object
        if (obj instanceof Object) {
            var copy = {};
            for (var attr in obj) {
                if (obj.hasOwnProperty(attr)) copy[attr] = GM.Form.CommonOper.cloneObject(obj[attr]);
            }
            return copy;
        }

        throw new Error("Unable to copy obj! Its type isn't supported.");
    },
    /**
     * @description 
     */
    showMapDialog: function () {
        if (window.frames['processMap'].pMap == null) {
            //
            $('#mapViewDialog').dialog({
                title: '',
                autoOpen: false,
                resizeable: true,
                draggable: true,
                show: {
                    effect: 'fadeIn',
                    duration: 500
                },
                hide: {
                    effect: 'fadeOut',
                    duration: 500
                },
                modal: false,
                width: 1200,
                height: 500,
                zIndex: 100,
                position: {
                    my: 'right bottom',
                    at: 'right bottom',
                    of: window
                }
            });
            var pLocation = parent.location;
            var strPathName = pLocation.pathname;
            if (strPathName.toLowerCase().substr(strPathName.length - 4, 4) === 'html') {
                strPathName = GM.FilePath.getDirName(strPathName);
            } else if (strPathName.toLowerCase().substr(strPathName.length - 1, 1) === '\/') {
                strPathName = strPathName.substr(0, strPathName.length - 1);
            }
            window.frames['processMap'].location.href = pLocation.origin + strPathName + '/map.html';
        }
        $('#mapViewDialog').dialog('open');
    }
};



