import 'agencyService';

angular.module('frameApp').controller('agencyCtrl', ['$scope', '$rootScope', '$agencyService', '$filter', function ($scope, $rootScope, $agencyService, $filter) {
    var userId = GM.userInfo.loginName;
    var appId = GM.AppInfo.id;
    $scope.pendingPage = {};
    $scope.agencyForm = {
        userId: userId,
        appId: appId,
        orderField: 'startTime'
    };

    //
    $scope.$watch('pendingPage.currentPage', function () {
        if ($scope.pendingPage.currentPage > 0) {
            $scope.agencyForm.current = $scope.pendingPage.currentPage;
            queryData();
        }
    });
    $scope.pendingPage.currentPage = 1;

    //
    function queryData () {
        $agencyService.queryPendingData($scope.agencyForm).then(function (response) {
            var dbData = response.data.records;
            angular.forEach(dbData, function (item, index) {
                item.index = index + 1;
                item.startTime = $filter('date')(item.startTime, 'yyyy-MM-dd HH:mm:ss');
            });
            $scope.userData.data.page.records = dbData;
            $scope.pendingPage.totalItems = response.data.total;
        }, function (erron) {
            GM.Infomation.show(erron.message, {
                type: 'warning'
            });
        });
    }

    /********/
    $scope.tagData = {
        tags: [{
            name: '',
            value: ''
        }, {
            name: '',
            value: ''
        }, {
            name: '',
            value: ''
        }],
        checkTag: 'checkTag'
    };

    $scope.checkTag = function (tags) {
        $scope.agencyForm.xmjd = [];
        for (var i = 0; i < tags.length; i++) {
            $scope.agencyForm.xmjd.push(tags[i].value);
        }
        queryData();
    };

    //
    $scope.userData = {
        config: {
            'taskKey': {
                'type': 'a',
                'click': 'goProcess'
            }
        },
        data: {
            page: {},
            head: {
                'index': '',
                'bizKey': '',
                'bizName': '',
                'processName': '',
                'startTime': '',
                'submitterName': '',
                'taskKey': ''
            }
        },
        headList: ['index', 'bizKey', 'bizName', 'processName', 'startTime', 'submitterName', 'taskKey']
    };

    var getExtraBizFields = function () {
        return $agencyService.queryExtraBizFields(appId).then(function (res) {
            if (res.success === true && res.data && res.data.length > 0) {
                for (var i = 0; i < res.data.length; i++) {
                    $scope.userData.headList.splice(4, 0, res.data[i].field);
                    $scope.userData.data.head[res.data[i].field] = res.data[i].label;
                }
            }
        }, function (res) {});
    };

    //
    $scope.refer = function () {
        queryData();
    };

    //
    $scope.delet = function () {
        $scope.agencyForm = {
            userId: userId
        };
        queryData();
    };

    // 
    $scope.goProcess = function (item) {
        console.log(item);
        $rootScope.processUrl = $agencyService.getHtmlPath() + '?appId=' + appId + '&serviceAppKey=' + item.serviceAppKey + '&box=agency&ywh=' + item.bizKey + '&processKey=' + item.processKey + '&processDefId=' + item.processDefId + '&taskKey=' + encodeURIComponent(item.taskKey ? item.taskKey : '') + '&processName=' + encodeURIComponent(item.processName) + '&caseTitle=' + encodeURIComponent(item.bizName);
        $("[ng-controller='agencyCtrl'] *").hide();
        $("[ng-controller='agencyCtrl']").append('<iframe frameborder="0" height="100%" width="100%" name ="agencyCtrl"></iframe>');
        window['agencyCtrl'].location.href = $rootScope.processUrl;
    };

    //
    $scope.goPending = function () {
        $scope.activeNaviFromPath('/agency', true);
    };
}]);



