angular.module('frameApp').factory('$draftService', ['$gmRestful', function ($gmRestful) {
    var result = {
        //
        queryPendingData: function (params) {
            var pRestful = new $gmRestful();
            var pPromise = pRestful.get(ACTIVITI_SERVER + 'userdraft/getDraftInfo', params);
            return pPromise;
        }
    };
    return result;
}]);



