angular.module('frameApp').directive('tabTop', function () {
    return {
        restrict: 'EAC',
        template: '<div class="form-top-div clearfix"><div class="form-top-left pull-left"><div ng-if="!taskInfo.forbid"><h3>{{taskInfo.processName}}&nbsp;&nbsp;&nbsp;{{taskInfo.taskKey}}</h3><span class="label"ng-if="!taskInfo.isClaimed"></span><span class="label label-success"ng-if="taskInfo.isClaimed"></span></div><div ng-if="taskInfo.forbid"><h3>{{taskInfo.processName}}</div></div><div class="form-top-right pull-right"ng-init="operatList = true"ng-init="print = true"><div class="dropdown"><i class="icon icon-list-ul more"data-toggle="dropdown"title=""></i><ul class="dropdown-menu pull-right"><li class="dropdown"ng-if="!taskInfo.forbid && !taskInfo.isClaimed"ng-click="claim(true);"><a href="javascript:;"></a></li><li class="dropdown"ng-if="!taskInfo.forbid && taskInfo.isClaimed"ng-click="claim(false)"><a href="javascript:;"></a></li><li class="divider"></li><li class="dropdown-submenu"ng-click="showWfGraph()"><a href="javascript:;"></a></li><li class="divider"></li><li class="dropdown"><a href="javascript:;"></a><ul class="dropdown-menu pull-left"><li ng-repeat="item in printItems"><a href="javascript:;"ng-click="print(item.code)">{{item.name}}</a></li></ul></li><li class="divider"></li><li class="dropdown"ng-click="viewMap()"><a href="javascript:;"></a></li></ul></div></div></div><hr style="margin-top: 0">',
        link: function (scope, element, attr, transclution) {

        }
    };
});



