import 'agencyService';

angular.module('frameApp').controller('completeCtrl', ['$scope', '$rootScope', '$agencyService', '$filter', function ($scope, $rootScope, $agencyService, $filter) {
    var userId = GM.userInfo.loginName;
    var appId = GM.AppInfo.id;

    $scope.completeForm = {
        userId: userId,
        appId: appId,
        orderField: 'completeTime'
    };

    //
    $scope.pendingPage = {
        currentPage: 1
    };
    $scope.$watch('pendingPage.currentPage', function () {
        if ($scope.pendingPage.currentPage > 0) {
            $scope.completeForm.current = $scope.pendingPage.currentPage;
            queryData();
        }
    });
    $scope.pendingPage.currentPage = 1;

    //
    function queryData () {
        $agencyService.queryCompleteData($scope.completeForm).then(function (response) {
            var dbData = response.data.records;
            angular.forEach(dbData, function (item, index) {
                item.index = index + 1;
                item.completeTime = $filter('date')(item.completeTime, 'yyyy-MM-dd');
            });
            $scope.userData.data.page.records = dbData;
            $scope.pendingPage.totalItems = response.data.total;
        }, function (erron) {
            GM.Infomation.show(erron.message, {
                type: 'warning'
            });
        });
    }

    /********/
    $scope.tagData = {
        tags: [{
            name: '',
            value: 0
        }, {
            name: '',
            value: 1
        }, {
            name: '',
            value: 2
        }],
        checkTag: 'checkTag'
    };

    $scope.checkTag = function (tags) {
        $scope.completeForm.xmjd = [];
        for (var i = 0; i < tags.length; i++) {
            $scope.completeForm.xmjd.push(tags[i].name);
        }
        queryData();
    };

    //
    $scope.userData = {
        config: {
            operate: [{
                name: '',
                icon: 'icon-edit',
                click: 'goProcess'
            }]
        },
        data: {
            page: {},
            head: {
                'index': '',
                'bizKey': '',
                'bizName': '',
                'processName': '',
                'taskKey': '',
                'completeTime': '',
                'targetTaskKey': '',
                'receiverName': ''
            }
        },
        headList: ['index', 'bizKey', 'bizName', 'processName', 'taskKey', 'completeTime', 'targetTaskKey', 'receiverName']
    };

    $scope.refer = function () {
        queryData();
    };

    //
    $scope.delet = function () {
        $scope.completeForm = {
            userId: userId
        };
        queryData();
    };

    // 
    $scope.goProcess = function (item) {
        console.log(item);
        $rootScope.processUrl = 'forms/index.html?appId=' + appId + '&serviceAppKey=' + item.serviceAppKey + '&box=complete&ywh=' + item.bizKey + '&processKey=' + item.processKey + '&processDefId=' + item.processDefId + '&taskKey=' + encodeURIComponent('') + '&processName=' + encodeURIComponent(item.processName) + '&caseTitle=' + encodeURIComponent(item.bizName);
        $("[ng-controller='completeCtrl'] *").hide();
        $("[ng-controller='completeCtrl']").append('<iframe frameborder="0" height="100%" width="100%" name ="completeCtrl"></iframe>');
        window['completeCtrl'].location.href = $rootScope.processUrl;
    };

    //
    $scope.goPending = function () {
        $scope.activeNaviFromPath('/complete', true);
    };
}]);



