import 'draftService';

angular.module('frameApp').controller('draftCtrl', ['$scope', '$location', '$rootScope', '$draftService', '$agencyService', '$log', function ($scope, $location, $rootScope, $draftService, $agencyService, $log) {
    var userId = GM.userInfo.loginName;
    $scope.draftForm = {
        userId: userId
    };

    //
    $scope.regions = GM.userInfo.areas;
    $scope.treearea = {
        name: 'title',
        children: 'childrens',
        attvalue: 'code'
    };

    //
    $scope.pendingPage = {
        currentPage: 1
    };

    $scope.$watch('pendingPage.currentPage', function () {
        if ($scope.pendingPage.currentPage > 0) {
            $scope.draftForm.current = $scope.pendingPage.currentPage;
            queryData();
        }
    });
    $scope.pendingPage.currentPage = 1;

    //
    function queryData () {
        $agencyService.queryDraftData($scope.draftForm).then(function (response) {
            var cgData = response.data.page.records;
            angular.forEach(cgData, function (item, index) {
                item.index = index + 1;
            });
            $scope.userData.data.page.records = cgData;
            $scope.pendingPage.totalItems = response.data.page.total;
        }, function (erron) {
            GM.Infomation.show(erron.message, {
                type: 'warning'
            });
        });
    }

    /********/
    $scope.tagData = {
        tags: [{
            name: '',
            value: ''
        }, {
            name: '',
            value: ''
        }, {
            name: '',
            value: ''
        }, {
            name: '',
            value: ''
        }, {
            name: '',
            value: ''
        }, {
            name: '',
            value: ''
        }, {
            name: '',
            value: ''
        }, {
            name: '',
            value: ''
        }],
        checkTag: 'checkTag'
    };

    $scope.checkTag = function (tags) {
        $scope.draftForm.xmjd = [];
        for (var i = 0; i < tags.length; i++) {
            $scope.draftForm.xmjd.push(tags[i].value);
        }
        queryData();
    };

    //
    $scope.userData = {
        config: {
            operate: [{
                name: '',
                icon: 'icon-edit',
                click: 'goProcess'
            }]
        },
        data: {
            page: {},
            head: {
                'index': '',
                'xmysubh': '',
                'xmmc': '',
                'xzqmc': '',
                'zgm': '',
                'ztz': '',
                'xmjd': '',
                'xmlx': ''
            }
        },
        headList: ['index', 'xmysubh', 'xmmc', 'xzqmc', 'zgm', 'ztz', 'xmjd', 'xmlx']
    };

    //
    $scope.refer = function () {
        queryData();
    };

    //
    $scope.delet = function () {
        $scope.draftForm = {
            userId: userId
        };
        queryData();
    };

    //
    $scope.goPending = function () {
        $scope.activeNaviFromPath('/draft', true);
    };

    // 
    $scope.goProcess = function (item) {
        $rootScope.processUrl = $agencyService.getHtmlPath() + '?did=' + item.id + '&box=draft&processid=' + encodeURIComponent(item.processKey);
        $("[ng-controller='draftCtrl'] *").hide();
        $("[ng-controller='draftCtrl']").append('<iframe frameborder="0" height="100%" width="100%" name ="draftCtrl"></iframe>');
        window['draftCtrl'].location.href = $rootScope.processUrl;
    };
}]);



