angular.module('frameApp').factory('$agencyService', ['$gmRestful', function ($gmRestful) {
    var result = {
        queryExtraBizFields: function (appId) {
            return new $gmRestful().get(ACTIVITI_SERVER + 'usertask/getExtraBizFields?appId=' + appId);
        },
        //
        queryStartData: function (params) {
            var pRestful = new $gmRestful();
            var pPromise = pRestful.get(RESTURL.process.start, params);
            return pPromise;
        },
        //
        queryPendingData: function (params) {
            var pRestful = new $gmRestful();
            var pPromise = pRestful.get(ACTIVITI_SERVER + 'usertask/list/handling', params);
            return pPromise;
        },
        //
        getQuery: function (params) {
            var pRestful = new $gmRestful();
            var pPromise = pRestful.get(RESTURL.process.query, params);
            return pPromise;
        },
        //
        processDate: function (timeStamp) {
            var date = new Date(timeStamp);
            return [date.getFullYear(), date.getMonth() + 1, date.getDate()].join('-');
        },
        //
        queryCompleteData: function (params) {
            var pRestful = new $gmRestful();
            var pPromise = pRestful.get(ACTIVITI_SERVER + 'usertask/list/handled', params);
            return pPromise;
        },
        //
        queryDraftData: function (params) {
            var pRestful = new $gmRestful();
            var pPromise = pRestful.get(ACTIVITI_SERVER + 'userdraft/getDraftInfo', params);
            return pPromise;
        },
        /**
         * @description 
         * @param processId
         * @param nodeId
         * @returns {*}
         */
        getHtmlPath: function () {
            var strResult = null;
            strResult = 'forms/index.html';
            return strResult;
        },
        //
        getProcessCatalogs: function (params, id) {
            var pRestful = new $gmRestful();
            var pPromise = pRestful.get(RESTURL.wf.getProcessCatalogs + (id ? ('?id=' + id) : ''), params);
            return pPromise;
        },
        //
        edit: function (params) {
            var pRestful = new $gmRestful();
            var pPromise = pRestful.post(RESTURL.wf.editProcessCatalog, params);
            return pPromise;
        },
        //
        delete: function (params, id) {
            var pRestful = new $gmRestful();
            var pPromise = pRestful.get(RESTURL.wf.deleteProcessCatalog + '?id=' + id, params);
            return pPromise;
        }
    };
    return result;
}]);



