angular.module('frameApp').factory('$formCoreService', ['$gmRestful', function ($gmRestful) {
    var service = {
        loadPage: function (scope, validation, filter, dlg, gmTable, timeout) {
            GM.Form.FormEngine.init(scope, new $gmRestful(), validation, filter, dlg, gmTable, timeout);
            GM.Form.FormData.init(scope.formIds);
            scope.getData();
        }
    };

    return service;
}]);



