angular.module('frameApp').factory('$protocolService', ['$gmRestful', function ($gmRestful) {
    var result = {
        queryDraft: function (appId, roles) {
            var pRestful = new $gmRestful();
            return pRestful.get(ACTIVITI_SERVER + '/bizconfig/getProcessCatalogs?appId=' + appId + '&permissions=' + encodeURIComponent(roles));
        },
        getUUID: function () {
            return new $gmRestful().get(ACTIVITI_SERVER + '/usertask/getUUID');
        }
    };

    return result;
}]);



